  .whiteTheme{
    background: #fff;
  }
  
  .mainView {
    position: relative;
    min-height: 1000px;
    z-index: 0;
    padding-top: 60px;
}
  
  
  .App {
    text-align: center;
  }
  
  .content_body{
    max-width: 1260px;    
  }
  
  
  .content_home{
    margin-top: 50px;
  }
  
  .content_details{
    margin-top: 50px;
  }
  
  .content_forms{
    background: var(--background--color--cards);
    padding: 25px 0;
  }
  

body {
    --heliumPlayer__color_blck_flx: #ff7817;
    --heliumPlayer__color_light_9: #f9f9f9; 
    margin: 0;
    padding: 0;  
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0px 0px;    
    background-color: var(--heliumPlayer__color_blck_flx);
    color: var(--heliumPlayer__color_light_9);
    font-family: Roboto,Helvetica,Arial,sans-serif;
   
    min-height: 400px;
}

  

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flexSpace{
    -ms-flex: 1 1 0.000000001px;
    -webkit-flex: 1;
    flex: 1;
    -webkit-flex-basis: 0.000000001px;
    flex-basis: 0.000000001px;
  }


.spaceAround {
    justify-content: space-around;
}

@-webkit-keyframes text-intro{0% {opacity: 0;transform: translateY(3vw);}100% {opacity: 1;transform: translateY(0);}}
@keyframes text-intro{0% {opacity: 0;transform: translateY(3vw);}100% {opacity: 1;transform: translateY(0);}}
@-webkit-keyframes repaint{0%{width: 99.999%;}100% {width: 100%;}}
@keyframes repaint{0%{width: 99.999%;}100% {width: 100%;}}
@-webkit-keyframes App-logo-spin {from {transform: rotate(0deg);}to {transform: rotate(360deg);}}
@keyframes App-logo-spin {from {transform: rotate(0deg);}to {transform: rotate(360deg);}}
@-webkit-keyframes fade-in{0%{opacity:0}100%{opacity:1}}
@keyframes fade-in{0%{opacity:0}100%{opacity:1}}
@-webkit-keyframes slide-left{0%{-webkit-transform:translateX(30%);transform:translateX(30%)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}
@keyframes slide-left{0%{-webkit-transform:translateX(30%);transform:translateX(30%)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}
@-webkit-keyframes slide-right{0%{-webkit-transform:translateX(-30%);transform:translateX(-30%)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}
@keyframes slide-right{0%{-webkit-transform:translateX(-30%);transform:translateX(-30%)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}
@-webkit-keyframes enter-text{0%{-webkit-transform:translateY(40px);transform:translateY(40px)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}
@keyframes enter-text{0%{-webkit-transform:translateY(40px);transform:translateY(40px)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}
@-webkit-keyframes enter-content{0%{-webkit-transform:translateY(25px);transform:translateY(25px)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}
@keyframes enter-content{0%{-webkit-transform:translateY(25px);transform:translateY(25px)}100%{-webkit-transform:translateY(0);transform:translateY(0)}}
@-webkit-keyframes pulse{0%,100%{opacity:1}50%{opacity:.2}}
@keyframes pulse{0%,100%{opacity:1}50%{opacity:.2}}
@-webkit-keyframes show-check-mark{0%{stroke-dashoffset:230%}100%{stroke-dashoffset:76%}}
@keyframes show-check-mark{0%{stroke-dashoffset:230%}100%{stroke-dashoffset:76%}}



@-webkit-keyframes gogoloader {
    0% {
      -webkit-transform: scale(0);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1);
      opacity: 0;
    }
  }
  
  @keyframes gogoloader{
    0% {
      transform: scale(0);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: scale(1);
      opacity: 0;
    }
  }


  @-webkit-keyframes bezeloader {
    0% {
      -webkit-transform: scale(0);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1.5);
      opacity: 0;
    }
  }
  
  @keyframes bezeloader {
    0% {
      transform: scale(0);
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: scale(1.5);
      opacity: 0;
    }
  }


